export const  spawnUser = ({name,idcard,sex,age,phone,education,address,person_detail,des,target_job,night_job,target_salary,percentage}) =>{
      var user = new Parse.User();
      user.set("parent", Parse.User.current());

      //
      user.set("name", name);
      //
      user.set("idcard", idcard + "");
      user.set("sex", sex);
      user.set("age", age + "");
      user.set("phone", phone);
      user.set("education", education);
      user.set("address", address);
      user.set("username", idcard + "");
      user.set("password", "123456");
      user.set("person_detail", person_detail);
      user.set("des", des);

      user.set("target_job", target_job);
      user.set("night_job", night_job);
      user.set("target_salary", target_salary);
     
      // 
      //  
      user.set("percentage",percentage)
      return user   
}

